.main {
    width: 100%;
    display: flex;
    align-items: flex-end;
    flex-direction: column;
}
.date {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-bottom: 24px;
    width: 100%;
}
.date h2 {
    margin-right: auto;
    color: #4B5EA3;
}
.date label {
    color: #37395C;
    font-family: 'Roboto';
}
.content {
    width: 100%;
    display: flex;
    flex-direction: column;
    row-gap: 24px;
    overflow-y: auto;
    max-height: calc(100vh - 228px);
    padding: 1px;
    box-sizing: border-box;
    padding-bottom: 28px;
}
.card {
    box-shadow: 0 0 10px -7px black;
    border-radius: 16px;
    /* display: flex; */
    justify-content: center;
    align-items: center;
    padding: 16px;
    box-sizing: border-box;

}
.mini {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 24px;
}
.mini .card {
    width: 33%;
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.miniSide {
    display: flex;
    flex-direction: column;
    gap: 8px;
}
.miniSide h3 {
    text-align: center;
}
.titleLabel {
    font-size: 16px;
    color: black;
    opacity: .4;
}
.value {
    font-size: 28px;
    color: #4B5EA2;
}
.miniIcon {
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 32px;
    color: white;
    border-radius: 50%;
}
.total .miniIcon {
    background-color: #4B5EA2;
}
.totalLastMonth .miniIcon {
    background-color: #E86589;
}
.totalEarn .miniIcon {
    background-color: #9DC8CF;
}
.users .miniIcon {
    background-color: #9DC8CF;
}
.popular {
    width: 50%;
    height: 250px;
    display: flex;
    align-items: flex-start;
}
.popularSide, .otherPopular {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 14px;
}
.popularWrapper {
    display: flex;
    flex-direction: column;
    gap: 8px;
}
.popularItem {
    display: flex;
    align-items: center;
    gap: 6px;
    color: #37395C;
}
.popularItem h3 {
    font-size: 14px;
    color: #37395C;
}
.popularItem h3::first-letter {
    text-transform: uppercase;
}
.topItem h3 {
    font-size: 20px;
    color: #4B5EA2;
}
.popularItem img {
    width: 50px;
    height: 50px;
    object-fit: cover;
    border-radius: 8px;
    margin-right: 6px;
}
.topItem img {
    width: 60px;
    height: 60px;
}
.popularItem label {
    font-weight: 700;
    color: #37395C;
}
.topItem label {
    font-size: 24px;
    color: #4B5EA2;
}
.buyCount {
    display: flex;
    align-items: center;
    gap: 8px;
}
.buyCount svg {
    background-color: #4B5EA2;
    border-radius: 50%;
    color: white;
    padding: 12px;
    font-size: 26px;
}
.buyCount p {
    font-size: 14px;
    font-weight: 400;
    color: #37395C;
    opacity: .4;
}
.popularSection {
    width: 100%;
    display: flex;
    gap: 24px;
}
.charts {
    width: 100%;
    display: flex;
    gap: 24px;
}
.otherPopular {
    gap: 32px;
}
.otherPopular .item {
    display: flex;
    flex-direction: column;
}
.otherPopular .item h3 {
    font-size: 16px;
    color: #4B5EA2;
    margin-top: 8px;
}
.promocodes {
    width: 20%;
}
.promocodes h3 {
    font-size: 16px;
    color: black;
    opacity: .4;
    font-weight: 400;
    margin-bottom: 16px;
}
.promocode {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.promocodeInfo {
    display: flex;
    align-items: center;
    gap: 8px;
}
.promocode p {
    font-size: 14px;
    color: #4B5EA2;
    font-weight: 700;
}
.promocode label {
    font-size: 14px;
    color: #37395C;
    font-weight: 500;
}
.empty {
    color: #4B5EA2;
    font-size: 16px;
}
.charts .empty {
    margin-top: 16px;
}
.ordersBar {
    width: 60%;
}
.donat {
    width: 40%;
    height: 250px;
}

@media screen and (max-width: 768px) {
    .mini {
        flex-direction: column;
    }
    .mini .card {
        width: 100%;
    }
    .charts {
        flex-direction: column;
    }
    .donat {
        width: 100%;
    }
    .ordersBar {
        width: 100%;
    }
    .popularSection {
        flex-direction: column;
    }
    .popular {
        width: 100%;
    }
    .promocodes {
        width: 100%;
    }
}

@media screen and (max-width: 568px) {
    .content {
        padding-bottom: 5px;
    }
    .donat {
        display: none;
    }
    .date label {
        display: none;
    }
}

@media screen and (max-width: 468px) {
    .topItem img {
        width: 50px;
        height: 50px;
    }
    .topItem h3 {
        font-size: 14px;
    }
    .value {
        font-size: 24px;
    }
}